import { Component, OnInit } from '@angular/core';
import { LoginUsers } from '../Store/LoginUsers';
import { ApicallingService } from '../apicalling.service';
import { HttpClient } from '@angular/common/http';
import { Store } from '@ngrx/store';
import { Router } from '@angular/router';
import * as PageStore from "../Store/PageStore/Page.Actions";
import Swal from 'sweetalert2';
import { AppComponent } from '../app.component';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  loggedUser : LoginUsers; 
  imagePath: string = "";
  FirstName : string ="";
  LastName : string ="";
  MobileNo : string ="";
  Email : string ="";
  Password  : string = "";
  ConfirmPassword : string = "";
  Id : number = 0;
  operation: string = "";
  submit: boolean = false;
  dbResult: any = [];
  msg: string = "";
  exist: string = "";
  status:boolean=false;
  imageUpload : string="";


  password : string = "";
  newPassword : string = "";
  newConfirmPassword : string = "";
  logUser: any = [];
  submit1 : boolean = false;
  success : boolean = false;


  constructor(private apiCall: ApicallingService, private http: HttpClient,private store:Store<any>,private router:Router,
    private app:AppComponent) {
    debugger;
    this.loggedUser = new LoginUsers();
    this.imagePath = this.apiCall.imagePath;
    if (this.store.source) {
      this.store.source.subscribe((data: any) => {
        debugger;
        const result2 = data['ooweb'].filter((x: any) => x.ViewName === 'loginusers');
          if(result2.length > 0){
            debugger;
            this.loggedUser = result2[0];
            this.Id = this.loggedUser.Id;
            this.FirstName = this.loggedUser.FirstName;
            this.LastName = this.loggedUser.LastName;
            this.MobileNo = this.loggedUser.MobileNo;
            this.Email = this.loggedUser.Email;
            this.Password = this.loggedUser.Password;
            this.ConfirmPassword = this.loggedUser.ConfirmPassword;
            this.Image= this.loggedUser.Image;
          }
      });
    }
    
  }

  ngOnInit(): void {
    this.imageUpload = this.apiCall.ImageUpload;

  }


  save(){
    this.submit = true;
    if (this.FirstName != '' && this.LastName != '' && this.MobileNo != '' && this.Email != '' && this.Password != '' && this.ConfirmPassword != '' && this.Password == this.ConfirmPassword) {
      debugger;
      this.msg = "";
      var data = ({ 'Id': this.Id, 'FirstName': this.FirstName, 'LastName': this.LastName, 'MobileNo': this.MobileNo, 'Email': this.Email, 'Password': this.Password, 'ConfirmPassword': this.ConfirmPassword,'Image': this.Image });
      var jsonData = JSON.stringify(data);
      if (this.Id == 0) {
        this.operation = "Insert";
      } else {
        this.operation = "Update";
      }
      this.apiCall.DBCalling(this.operation, "Loginusers", jsonData, "", "", "").subscribe(res => {
        debugger;
        this.dbResult = res;
        if (this.dbResult[0][0].DBresult > 0) {
          this.Login();
          if (this.Id == 0) {
            // this.Reset();
          }
        } else if (this.dbResult[0][0].DBresult == -1) {
          debugger;
          this.exist = "Email Already Exist!";
        }
      });
    } else if (this.Password != this.ConfirmPassword) {
      this.msg = "Invalid Password!";
    }
  }


  Login() {
    debugger;
    this.submit = true;
    if (this.Email != "" && this.Password != "") {
      this.apiCall.DBCalling('View', "Loginusers", this.Email, this.Password, "", 'User').subscribe(res => {
        debugger;
        this.dbResult = res;
        if (this.dbResult.length > 0) {
          debugger;
          this.loggedUser = this.dbResult[0][0];
          window.location.reload();
          // const modal = new bootstrap.Modal(document.getElementById('login'));
          // modal.hide();
          this.store.dispatch(new PageStore.OpenPage(Object.assign({}, this.loggedUser)));
          window.location.reload();
        } else {
          alert('Invalid credentials');
        }
      })
    }
  }

  
  Image : string ="";
  imageSrc:any;
  imageSrc1:string="";


  onFileChange(event:any){
    debugger;
    this.status = false
    const file = event.target.files[0];
    this.status = event.target.files.length>0?true:false

    var Imageurl= event.target.files[0];
    if(this.status==true){
       const reader = new FileReader();
       reader.readAsDataURL(file);
       reader.onload = () => {
          this.imageSrc = reader.result; 
          this.imageSrc1 = this.imageSrc
          this.saveImage();
       }
    }
  }

  async saveImage(){
    try{
      let value=this.Image;
        this.Image="";
      //  this.loader.loaderon()
        await this.http.post(this.imageUpload, {'image':this.imageSrc}).subscribe((res) => {
            debugger;
            this.dbResult = res;
           if(res==null){
            this.Image=value;
            // this.saveProduct()
           }else{
            debugger;
             this.Image =this.dbResult[0][1];
            //  this.saveProduct()
           }
          })
        }catch(error){
           console.log(error);
      }
  }


  validate(){
    debugger;
    this.submit = true;
    if(this.password == this.loggedUser.Password){
      this.success = true;
    }else{
      this.msg = "Wrong Password!";
    }
  }

  update(){
    debugger;
    this.submit1 = true;
    if(this.newPassword !='' && this.newConfirmPassword !='' && this.newPassword == this.newConfirmPassword){
      debugger;
      let order = JSON.stringify({'Password':this.newPassword,'ConfirmPassword':this.newConfirmPassword,'Id':this.loggedUser.Id});
      this.apiCall.DBCalling('updatePassword',"Loginusers",order,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        this.password = this.newConfirmPassword;
        // this.Login();
        this.reset();
        const changePasswordButton = document.getElementById("changePassword") as HTMLButtonElement;
        if (changePasswordButton) {
          changePasswordButton.click();
        }
        this.alert();
      });
    }
  }

  reset(){
    this.newPassword = "";
    this.newConfirmPassword = "";
    this.password = "";
    this.submit = false;
    this.submit1 = false;
    this.success = false;
  }
  
  alert(){
    Swal.fire({
      title: 'Password Changed',
      text: 'Please log in again.',
      icon: 'success',
      confirmButtonText: 'OK',
    }).then((result) => {
      if (result.isConfirmed) {
        this.app.logout();
        this.router.navigateByUrl('Home');
      }
    });
  }

  cancel(){
    this.reset();
  }

}
