import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpParams, HttpHeaders } from '@angular/common/http';

import * as AppSettings from './Store/Appsettings/AppSettings';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
};

@Injectable({
  providedIn: 'root'
})

export class ApicallingService {

  DBCallingURL = AppSettings.DBCallingURL;
  imagePath = AppSettings.imagePath;
  ImageUpload = AppSettings.ImageUpload;

  constructor(private http: HttpClient) {  }

  headers = new HttpHeaders();

  DBCalling(Operation:any, Xml:string, Xml1:any, Xml2:any, Xml3:any,Xml4:any) {
    debugger;
    var  URL='';
    URL=this.DBCallingURL;
    return this.http.post(URL, { Screen: Xml, Operation: Operation, Xml: Xml1, XmlChild1: Xml2, XmlChild2:Xml3,Search:Xml4},httpOptions);
  }
}
