import { Component, Injectable } from '@angular/core';
import { ApicallingService } from './apicalling.service';
import { HttpClient } from '@angular/common/http';
import { adminDetails } from './Store/loginuser';
import * as PageStore from './Store/PageStore/Page.Actions';
import { Store } from '@ngrx/store';
import { LoginUsers } from './Store/LoginUsers';
import { Router } from '@angular/router';
import { lstCart } from './Store/lstCart';
import { HomeComponent } from './home/home.component';
import { DatePipe } from '@angular/common';

@Injectable({
  providedIn:'root'
})

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'sdfsdf';
  imagePath: string = "";
  imageUpload: string = "";
  dbResult: any;
  lstAdmin: any = [];
  adminDetails : adminDetails;
  Logo: any;
  restaurant: any;
  loggedUser : LoginUsers; 
  lstCartStore : lstCart;
  active : string ="hide";
  year : any;

  constructor(private apiCall: ApicallingService, private http: HttpClient,private store:Store<any>,private router:Router,
    private _home:HomeComponent,private pipe:DatePipe) {
    debugger;
    this.imagePath = this.apiCall.imagePath;
    this.imageUpload = this.apiCall.ImageUpload;
    this.adminDetails = new adminDetails();
    this.lstCartStore = new lstCart();
    this.loggedUser = new LoginUsers();
    let date = new Date();
    this.year = this.pipe.transform(date,'YYYY');
    // alert(this.year)
    if (this.store.source) {
      this.store.source.subscribe((data: any) => {
        debugger;
        const result2 = data['ooweb'].filter((x: any) => x.ViewName === 'loginusers');
          if(result2.length > 0){
            this.loggedUser = result2[0];
          }
      });
    }
  }

  ngOnInit(){
    debugger;
    this.view();
  }
  
  view() {
    debugger;
    this.apiCall.DBCalling('View', "Admin", "", "", "", "").subscribe(res => {
      debugger;
      this.dbResult = res;
      this.lstAdmin = this.dbResult[0][0];
      this.Logo = this.imagePath + this.lstAdmin.Logo;
      this.restaurant = this.imagePath + this.lstAdmin.Image;
      this.adminDetails  = this.lstAdmin;
      this.store.dispatch(new PageStore.OpenPage(Object.assign({},this.adminDetails)));

      
      const link = document.querySelector("link[rel*='icon']") as HTMLLinkElement || document.createElement('link');
      link.type = 'image/x-icon';
      link.rel = 'shortcut icon';
      link.href =  this.Logo;
      document.getElementsByTagName('head')[0].appendChild(link);

    })
  }

  open(d:any){
    debugger;
    const googleMapsUrl = `https://www.google.com/maps/search/?api=1&query=${encodeURIComponent(d)}`;
    window.open(googleMapsUrl, '_blank');
  }

  home(){
    this.router.navigateByUrl('Home');
  }

  logout(){
    debugger;
    this.loggedUser = new LoginUsers();
    this.store.dispatch(new PageStore.OpenPage(Object.assign({},this.loggedUser)));
    this.lstCartStore = new lstCart();
    this.store.dispatch(new PageStore.OpenPage(Object.assign({},this.lstCartStore)));
    this.router.navigateByUrl('Home');
  }

  openPopup(){
    debugger;
    this._home.openPop();
  }

  navopen(){
    debugger;
    if(this.active =="hide"){
      this.active ="show"
    }else{
      this.active ="hide"
    }
  }

  myOrders(){
      this.router.navigateByUrl('my-orders');
  }

  profile(){
    this.router.navigateByUrl('profile');
  }

  
}
