import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { LoginUsers } from '../Store/LoginUsers';
import { ApicallingService } from '../apicalling.service';

@Component({
  selector: 'app-my-orders',
  templateUrl: './my-orders.component.html',
  styleUrls: ['./my-orders.component.css']
})
export class MyOrdersComponent implements OnInit {
  loggedUser : LoginUsers; 
  search: string = "";
  dbResult: any = [];
  lstOrders: any = [];
  lstOrderDetails : any = [];
  lstAdmin : any = [];
  lstOrders1 : any = [];

  constructor(private store:Store<any>,private apiCall: ApicallingService,) {
    debugger;
    this.loggedUser = new LoginUsers();
    if (this.store.source) {
      this.store.source.subscribe((data: any) => {
        debugger;
        const result2 = data['ooweb'].filter((x: any) => x.ViewName === 'loginusers');
          if(result2.length > 0){
            debugger;
            this.loggedUser = result2[0];
          }
      });
    }
    this.Admin();
  }

  ngOnInit(): void {
    debugger;
    this.viewOrders();
  }

  viewOrders(){
    debugger;
    this.apiCall.DBCalling('myOrders', "Orders", "", "", "", this.loggedUser.Id).subscribe(res => {
      debugger;
      this.dbResult = res;
      this.lstOrders = this.dbResult[0];
      this.lstOrders1 = this.lstOrders.filter((x:any)=> x.OrderStatus === 'Ready for Pick up' || x.OrderStatus === 'Out for Delivery' || x.OrderStatus === '');
    });
  }

  onExpandChange(id: any): void {
    debugger;

      this.apiCall.DBCalling('orderDetails',"Orders","","","",id).subscribe(res=>{
        debugger;
        this.dbResult = res;
        this.lstOrderDetails = this.dbResult[0];
      })
  }

  Admin(){
    this.apiCall.DBCalling('View', "Admin", "", "", "", "").subscribe(res => {
      debugger;
      this.dbResult = res;
      this.lstAdmin = this.dbResult[0][0];
    })
  }

  orderType(e:number){
    debugger;
    if(e==1){
      this.lstOrders1 = this.lstOrders.filter((x:any)=> x.OrderStatus === 'Picked up' || x.OrderStatus === 'Delivered');
    }
    if(e==2){
      this.lstOrders1 = this.lstOrders.filter((x:any)=> x.OrderStatus === 'Ready for Pick up' || x.OrderStatus === 'Out for Delivery' || x.OrderStatus === '');
    }
    if(e==3){
      this.lstOrders1 = this.lstOrders.filter((x:any)=> x.OrderStatus === 'Cancelled');
    }
  }

}
